import React,{ useEffect, useState } from 'react'
import axios from 'axios';
import useInputChange from '../hooks/useInputChange';
import Pokemons_container from './Pokemons_container';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import { Button } from '@mui/material';
import '../css/index.css'

const Pokemons_create = ({setPokeStat, newPokemon}) => {


  const [pokeData, setPokeData] = useState({});

  const [ input, handleInputChange ] = useInputChange({ });

  const [ inputStats, inputStatstChange ] = useInputChange('');

  const [ inputFile, inputFileChange ] = useState({});



  const formHandleSubmitPokemon = (e) => {
    const poke = { ...input }
    poke.stats = inputStats;
  
    setPokeData({ pokemon:poke })
    // savePokemon(poke)
    savePokemon(poke)
  }


  const savePokemon = (pokeData) => {

    const url = 'https://poke-apix.herokuapp.com/api/pokemon';
    // const url = 'http://localhost:8080/api/pokemon';
    // const urlImg = 'https://poke-apix.herokuapp.com/api/upload/pokemons/6345f66e12bd80e56e9c68b7'
    const config ={
      headers:{
          poke_token:'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYzMWMwODY5OTRjMDg4M2I4NjBjYjkzOSIsImlhdCI6MTY2NTQ0NzI0MywiZXhwIjoxNjY2MDUyMDQzfQ.4MJWBt-pr2VAABLz3PNk01BZypfwaYZ6CNUH4Zq1Ynw',
      }
    }


    axios.post( 
        url, 
        pokeData,
        config
    )
    .then((resp)=>{
      setPokeStat({pokemon:resp.data})
      return resp.data
    })
    .then(poke => {
      const { id } = poke
      const url = `https://poke-apix.herokuapp.com/api/upload/pokemons/${id}`
      const data = new FormData();
      data.append("archive",[...inputFile][0])

      
      axios.put(
        url,
        data,
        config
      ).then()
      .catch(console.log)
    })
    .catch(({response})=>{
        const { data } = response;
        console.log(data)
        return
    })
    
  }


  return (
    <div className='pokemon_create'>
      <form onSubmit={formHandleSubmitPokemon}>
         
      <ul>
        <li className='pokemon'>
          <h3>Pokemon</h3>
          <ul>
            <li className='input'><TextField id="outlined-basic" label="Nombre" variant="outlined" type="text" name="name" onChange={ handleInputChange } required /></li> 
            <li className='input'><TextField id="outlined-basic" label="Tipo" variant="outlined" type="text" name="type" onChange={ handleInputChange }  required /></li> 
            <li className='input'><TextField id="outlined-basic" label="#ID" variant="outlined" type="text" name="numberID" onChange={ handleInputChange }  required /></li> 
            <li className='input'><TextField id="outlined-basic" label="Generacion" variant="outlined" type="text" name="generation" onChange={ handleInputChange } placeholder={'Generacion'} required /></li> 

          </ul>
        </li>
        <li class="stats">
          <h3>Stats</h3>
          <ul >
            <li className='input'><TextField id="outlined-basic" label="Puntos de vida" variant="outlined" type="text" name="hp" onChange={ inputStatstChange }  required /></li> 
            <li className='input'><TextField id="outlined-basic" label="Ataque" variant="outlined" type="text" name="attack" onChange={ inputStatstChange }  required /></li> 
            <li className='input'><TextField id="outlined-basic" label="Defensa" variant="outlined" type="text" name="defense" onChange={ inputStatstChange } required /></li> 
            <li className='input'><TextField id="outlined-basic" label="Ataque especial" variant="outlined" type="text" name="special_attk" onChange={ inputStatstChange }  required /> </li>
            <li className='input'><TextField id="outlined-basic" label="Defensa especial" variant="outlined" type="text" name="special_defense" onChange={ inputStatstChange }  required /></li>
            <li className='input'><TextField id="outlined-basic" label="Velocidad" variant="outlined" type="text" name="velocity" onChange={ inputStatstChange }   required /></li>

          </ul>
        </li>
        <li>
          <h3>Imagen</h3>
          <li className='input'><TextField id="outlined-basic"  variant="outlined" type="file" class="archive" name="archive" onChange={ (e)=>{ inputFileChange(e.target.files)} }   required /></li>
        </li>
        <li><Button type='submit'>Crear pokemon</Button></li>
      </ul>
      </form>
    </div>
  )
}

export default Pokemons_create